
.react-activity-calendar {
	/* border: 1px solid white; */
	width:90vw;
	height: auto;
	margin-left:50px;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-family: var(--FF);
	background-color: black;
	min-height: 100vh;
	color: #fff;
	padding: 80px 60px 0px 60px;
}
.react-activity-calendar h1 {
	font-weight: 750;
	font-size: 3.2rem;
	color: white;
    font-family: var(--FF-1);
    padding-bottom: 10px;
}
.react-activity-calendar h1::after {
    content: "";
    height: 1px;
    width: 100%;
    background-color: orange;
    display: inline-block;
    box-shadow: 0px 5px 10px #6abdf97c;
	margin-bottom: 20px;
}

.gridcontainer{
    /* margin: auto; */
     gap: 50px;
     margin-left: 50px;
}

#github-calendar{
	/* border: 2px solid red; */
	height: 35vh;
	width: 80vw;
}

@media (max-width:1200px){
	#github-calendar{
		/* border: 2px solid red; */
		height: auto;
		width: auto;
	}

	.react-activity-calendar{
		margin: auto;
	}
}