


.project {
	/* border: 1px solid rgb(175, 19, 19); */
	width: 90vw;
    height: auto;
    display: flex;
    flex-direction: column;
	align-items: center;
	font-family: var(--FF);
	background-color: black;
	/* min-height: 100vh; */
	color: #fff;
    margin-left:50px; 
	/* padding: 80px 60px 0px 60px; */
    padding-top: 60px;
}

/* .project-title{
    display: flex;
    gap: 5px;
} */
/* .project-title::first-line{
    background-color: white;
} */

.project h1 {
    border-bottom: 2px solid orange;
    /* padding: 20px; */
	font-weight: 750;
	font-size: 3.2rem;
	color:white;
    font-family: var(--FF-1);
    padding-bottom: 10px;
    display: flex;
   align-items: center;
}
.project h1::after {
    content: "";
    height: 1px;
    width: 100%;
    background-color: orange;
    display: inline-block;
    box-shadow: 0px 5px 10px #6abdf97c;
    margin-bottom: 20px;
}

.container-card{
    width: 85vw;
    height: auto;
    display: grid;
    grid-template-columns: repeat(1,1fr);   
    gap: 25px;
    /* border: 1px solid rgb(255, 177, 177); */
}

.project-card{
    /* border: 1px solid green; */
    border-radius: 10px;
    width: 85vw;
    height: auto;
    padding: 10px;
    background-color: #373a3c7c;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.project-card h2{
    color:rgb(236, 234, 231) ;
    padding-bottom: 10px;
    padding-top: 10px;
    font-weight: 700;
    font-family: cursive;
}

.project-card h4{
    color:rgb(173, 177, 177);
    padding-bottom: 10px;
}


.lower{
    display: flex;
    /* justify-content: space-between; */
    gap: 20px;
    /* background-color: orange; */
    padding-top: 10px;
   

}

.lower a{
    text-decoration: none;
    /* color: black; */
    font-weight: bold;

}



#gitlink{
    color: white;
    font-size: small;
}
.project-github-link{
    display: flex;
    border: 0.5px solid white;
    width: 120px;
    height: 50px;
    border-radius: 35px;
    padding-top: 14px;
    padding-left: 10px;
    gap: 15px;
    background-color:rgb(35, 35, 33);
    /* color: white; */
    /* text-decoration: none; */
}
.project-github-link img{
    border-radius: 50%;
}

.project-deployed-link{
    display: flex;
    border: 0.5px solid white;
    width: 175px;
    height: 50px;
    border-radius: 35px;
    padding-top: 14px;
    padding-left: 10px;
    gap: 15px;
    background-color:rgb(35, 35, 33);
}

.project-deployed-link img{
    border-radius: 50%;
}

